<template>
    
</template>

<script>
    export default {
        name: "logout",
        created() {
            const _self = this;
            _self.showLoading();
            _self.$api('logout', {}, function (reData) {
                _self.logoutAndGoHome();
            }, function (error) {
                _self.logoutAndGoHome();
            }, false, false);
        },
        methods:{
            logoutAndGoHome() {
                this.$accessToken(null);
                window.location.href = this.$C('serverUrlAjax', 'sys') + '/exusers/u8_login.php?idweb='+this.$getWebId()+'&lang='+this.$langID();
            },
            showLoading() {
                this.$loading({
                    lock: true,
                });
            },
        }
    }
</script>

